import FooterInfo from '../component/FooterInfo';
import './Footer.scss';


const Footer = ()=>{

  const info = [
    { id: 1, src: "./img/symbol_mail.png", alt:'이메일 아이콘', href:"javascript:void()", children:'yoonjieut@gmail.com'},
    { id: 2, src: "./img/symbols_mapping.png", alt:'지도 아이콘', href:"javascript:void()", children:'대전광역시 서구 도안동'},
    { id: 3, src: "./img/symbol_birthday.png", alt:'생일 아이콘', href:"javascript:void()" , children:'1996-06-11'},
    { id: 4, src: "./img/symbol_pencil.png", alt:'학력 아이콘', href:"javascript:void()", children:`우송정보대학(언어치료/사회복지)`},
    { id: 5, src: "./img/symbols_phone-enabled-sharp.png", alt:'핸드폰 아이콘', href:"javascript:void()", children:'010-4412-3007'},
    { id: 6, src: "./img/symbol_github.png", alt:'깃허브 아이콘', href:"https://github.com/YoonJieut", children:'YoonJieut'}
  ]

  const infoList = 
  info.map( e => 
    <FooterInfo key={e.id} src={e.src} alt={e.alt} href={e.href} >
      {e.children}
    </FooterInfo>
  )

  return(
    <>
      <div id="Footer" className="fullPage">
        <div className='inner'>
          <div className='footerTop'>
            <div className='footerLText'>
              <h1>이런 사람이<br/>
                되고자 합니다.
              </h1>
              <h2>앞으로 나아가는 개발자 <span>윤준현</span></h2>
            </div>
            <span className='vertical-line'></span>
            <div className='footerRText'>
              <p>소통에 있어서 편안한 분위기를 조성하고, 맡은 일에 책임감 있는 사람</p>
              <p>사용자 입장에서 생각하고 웹을 만들어 가는데 재미를 느끼는 사람</p>
              <p>배우는 것에 흥미있고 좋아하는 일을 즐기며 잘하는 사람</p>
              <p><span>성장하지만 겸손을 잃지 않는 사람</span></p>
            </div>
          </div>
    
          <div className='footerBottom'>
            <div className='cul_line'>
              {infoList}
            </div>
          </div>
        </div> {/*inner End*/}
      </div>
    </>
  );
}
export default Footer;